@charset "utf-8";
body{
  margin: 0;
}

main h2{
  text-align: center;
}
ul{
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  margin: 10px auto 40px; padding: 0;
  width: 100%;
}
ul li {
  width: 45%; min-height: 400px;
  list-style: none;
  box-sizing: border-box;
  position: relative;
}
ul img{
  width: 100%; height: 260px;
  object-fit: cover;
  border-radius: 6px;
}
ul p{
  width: 100%;
  text-align: left;
}

ul p:nth-of-type(2){
  position: absolute;
  left: 0px;
  bottom: 0px;
  font-size: 18px;
  font-weight: bold;
}
ul p:last-of-type{
  position: absolute;
  right: 0; bottom: 0;
  text-align: end;
}
ul input{
  border: none;
  border-radius: 4px;
  padding: 8px 15px;
  background: #ff4242;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}
.inputData{
  width: 100%;
  margin: auto;
  padding: 15px;
  box-sizing: border-box;
  position: relative;
}
.inputData input{
  padding: 8px;
}
input[type="text"]{
width: calc(100% - 20px);
margin: 6px 0;
}
.inputData p{
text-align: left;
}
.inputData p:last-of-type{
  text-align: end;
}
.inputData p:last-of-type input{
  border: none;
  border-radius: 4px;
  font-weight: bold;
  padding: 8px 16px;
  background: #9b5b36;
  color: #fff;
  cursor: pointer;
}
.ex{
  font-size: 14px;
  color: #aa7455;
position: absolute;
right: 18px;
}
label{
  font-weight: bold;
}
